/* eslint no-undef: 0 */
import React, { useState } from 'react';
import {
  Chart as ChartJS,
  registerables
} from 'chart.js';
import WeatherGraph from './WetherGaph';

ChartJS.register(...registerables);

const getDateInformation = (month, day, dateList, dataList) => {
  const filteredList = [];

  for (let i = 0; i < dataList.length; i++) {
    const date = new Date(dateList[i]);
    if (date.getMonth() + 1 === month &&
        date.getDate() === day) {
      filteredList.push(dataList[i]);
    }
  }

  return filteredList;
}

const positionData = [
  {name: '日立', id: 'latitude=36.6&longitude=140.65&'},
  {name: '大甕', id: 'latitude=36.513&longitude=140.62&'},
  {name: 'グレースライフ池の川', id: 'latitude=36.58&longitude=140.64&'},
  {name: '水戸', id: 'latitude=36.37&longitude=140.476&'},
  {name: '東京', id: 'latitude=35.68&longitude=139.767&'},
]

const nameToID = (station) => {
  for (const value of positionData) {
    if (value.name === station) {
      return value.id;
    }
  }
  console.log('Error: ' + station + 'is not found.');
  return -1;
}

function Weather() {
  const [position, setPosition] = useState(nameToID('日立')); // 地点
  const [timeData, setTimeData] = useState([]); // 日時
  const [temperatureData, setTemperatureData] = useState([]); // 気温
  const [relativehumidityData, setRelativehumidityData] = useState([]); // 湿度
  const [apparentTemperatureData, setApparentTemperatureData] = useState([]); // 体感気温
  const [precipitationProbabilityData, setPrecipitationProbabilityData] = useState([]); // 降水確率
  const [precipitationData, setPrecipitationData] = useState([]); // 降水量
  const [weathercodeData, setWeathercodeData] = useState([]); // 天気
  const [dailyData, setDailyData] = useState(null);

  const doChangePosition = (event) => {
    setPosition(nameToID(event.target.value));
  }

  const doClick = () => {
    const fetchData = async () => {
      try {
        const response = await fetch('https://api.open-meteo.com/v1/forecast?'+position+'hourly=temperature_2m,relativehumidity_2m,apparent_temperature,precipitation_probability,precipitation,weathercode&daily=weathercode&timezone=Asia%2FTokyo');
        const jsonData = await response.json();
        const daily_data = jsonData['daily'];
        const hourly_data = jsonData['hourly'];
        setDailyData(daily_data['time']);
        const timeList = [];
        const temperatureList = [];
        const relativehumidityList = [];
        const apparentTemperatureList = [];
        const precipitationProbabilityList = [];
        const precipitationList = [];
        const weathercodeList = [];
        for (let i = 0; i < daily_data['time'].length; i++) {
          const date = new Date(daily_data['time'][i]);
          const _month = date.getMonth() + 1;
          const _day = date.getDate();
          timeList.push(getDateInformation(_month,_day,hourly_data['time'],hourly_data['time']));
          temperatureList.push(getDateInformation(_month,_day,hourly_data['time'],hourly_data['temperature_2m'])); 
          relativehumidityList.push(getDateInformation(_month,_day,hourly_data['time'],hourly_data['relativehumidity_2m']));
          apparentTemperatureList.push(getDateInformation(_month,_day,hourly_data['time'],hourly_data['apparent_temperature'])); 
          precipitationProbabilityList.push(getDateInformation(_month,_day,hourly_data['time'],hourly_data['precipitation_probability']));
          precipitationList.push(getDateInformation(_month,_day,hourly_data['time'],hourly_data['precipitation'])); 
          weathercodeList.push(getDateInformation(_month,_day,hourly_data['time'],hourly_data['weathercode']));
        }
        setTimeData(timeList);
        setTemperatureData(temperatureList);
        setRelativehumidityData(relativehumidityList);
        setApparentTemperatureData(apparentTemperatureList);
        setPrecipitationProbabilityData(precipitationProbabilityList);
        setPrecipitationData(precipitationList);
        setWeathercodeData(weathercodeList);
      } catch (error) {
        console.log('Error fetching data:', error);
      }
    };

    fetchData();
  }
  
  return (
    <div className='container'>
      <div className="mb-3">天気</div>
      <select className="form-select mb-3" onChange={doChangePosition} defaultValue="日立">
          {positionData.map((value) => (
            <option>{value.name}</option>
          ))}
      </select>
      <button type="button" className="btn btn-primary btn mb-3" onClick={doClick}>検索</button>
      { dailyData && 
        dailyData.map((item, index) => (
          <div>{item}
          <WeatherGraph timeData={timeData[index]}
                        temperatureData={temperatureData[index]}
                        relativehumidityData={relativehumidityData[index]}
                        apparentTemperatureData={apparentTemperatureData[index]}
                        precipitationProbabilityData={precipitationProbabilityData[index]}
                        precipitationData={precipitationData[index]}
                        weathercodeData={weathercodeData[index]} /></div>
        ))
      }
    </div>
  );
}

export default Weather;