/* eslint no-undef: 0 */
import React, { useState } from "react";
import HitachiMap from "./HitachiMap";
import TagaMap from "./TagaMap";

function RealtimeAllBus() {
  const stopdata = [
    {name: '兎平', id: 1833},
    {name: '日立駅中央口', id: 1825},
    {name: '大甕駅西口', id: 2118},
    {name: '大甕駅東口', id: 1964},
    {name: '大甕駅入口', id: 1995},
    {name: '大甕工場前', id: 1998},
    {name: '多賀駅前', id: 1844},
    {name: '成沢', id: 1835},
    {name: 'よっこら坂', id: 1978},
    {name: '日立市役所前', id: 1874},
    {name: '西宿', id: 2293}
  ]

  const poledata = [
    {name: '1番乗り場', id: 1},
    {name: '2番乗り場', id: 2},
    {name: '3番乗り場', id: 3},
    {name: '4番乗り場', id: 4}
  ]

  const nameToStopID = (stop) => {
    for (const value of stopdata) {
      if (value.name === stop) {
        return value.id;
      }
    }
    console.log('Error: ' + stop + 'is not found.');
    return -1;
  }

  const nameToPoleID = (pole) => {
    for (const value of poledata) {
      if (value.name === pole) {
        return value.id;
      }
    }
    console.log('Error: ' + pole + 'is not found.');
    return -1;
  }

  const [stop, setStop] = useState(nameToStopID('兎平'));
  const [pole, setPole] = useState(1);

  // stop が変更された場合の処理
  const doChangeStop = (event) => {
    setStop(nameToStopID(event.target.value));
  }
  
  // To が変更された場合の処理
  const doChangePole = (event) => {
    setPole(nameToPoleID(event.target.value));
  }

  const doClick = () => {
    var url = 'https://mc.bus-vision.jp/ibako/view/approachSpecifiedStop.html?';
    
    url += 'stopCdSpecified=' + stop + '&';
    url += 'poleCdSpecified=' + pole + '&';
    window.location.href = url;
  }

  return (
    <div className='container'>
      バス停通過全車両(現在地)
      <div className="input-group my-3">
        <select className="form-control col" onChange={doChangeStop} defaultValue="兎平">
          {stopdata.map((value) => (
            <option>{value.name}</option>
          ))}
        </select>
        <select className="form-control col" onChange={doChangePole} defaultValue="1番乗り場">
          {poledata.map((value) => (
            <option>{value.name}</option>
          ))}
        </select>
      </div>
      <button type="button" className="btn btn-primary btn mb-3" onClick={doClick}>検索</button>
      {stop === nameToStopID('日立駅中央口') && <HitachiMap />}
      {stop === nameToStopID('多賀駅前') && <TagaMap />}
    </div>
  )
}

export default RealtimeAllBus