/* eslint no-undef: 0 */
import React, { useState } from "react";
import DateTimePicker from "./DateTimePicker";
import HitachiMap from "./HitachiMap";
import TagaMap from "./TagaMap";

function Bus() {
  var currentDate = new Date();

  const stationdata = [
    {name: '兎平', id: 200421},
    {name: '日立駅中央口', id: 200429},
    {name: '大甕駅西口', id: 200912},
    {name: '大甕駅東口', id: 200222},
    {name: '大甕駅入口', id: 200224},
    {name: '大甕工場前', id: 200249},
    {name: '多賀駅前', id: 200307},
    {name: '成沢', id: 200401},
    {name: 'よっこら坂', id: 200410},
    {name: '日立市役所前', id: 200501},
    {name: '西宿', id: 200175}
  ]

  const nameToID = (station) => {
    for (const value of stationdata) {
      if (value.name === station) {
        return value.id;
      }
    }
    console.log('Error: ' + station + 'is not found.');
    return -1;
  }

  const [from, setFrom] = useState(nameToID('兎平'));
  const [to, setTo] = useState(nameToID('多賀駅前'));
  const [year, setYear] = useState(currentDate.getFullYear());
  const [month, setMonth] = useState(currentDate.getMonth() + 1);
  const [day, setDay] = useState(currentDate.getDate());
  const [hour, setHour] = useState(currentDate.getHours());
  const [min, setMin] = useState(currentDate.getMinutes());
  const [isReverse, setIsReverse] = useState(false);

  // From が変更された場合の処理
  const doChangeFrom = (event) => {
    setFrom(nameToID(event.target.value));
  }
  
  // To が変更された場合の処理
  const doChangeTo = (event) => {
    setTo(nameToID(event.target.value));
  }

  const doClick = (event) => {
    var url = 'http://bus.ibako.co.jp/route/result/?';
    
    const action_route_result = 1;
    const f_from_type = 1;
    const f_type_fromto = 1;
    const f_wait = 0;

    url += 'action_route_result=' + action_route_result + '&';
    url += 'f_from_type=' + f_from_type + '&';
    if (isReverse) {
      url += 'fs_from=' + to + '&';
      url += 'fs_to=' + from + '&';
    }
    else {
      url += 'fs_from=' + from + '&';
      url += 'fs_to=' + to + '&';
    }
    url += 'f_type_fromto=' + f_type_fromto + '&';
    url += 'f_date_Year=' + year + '&';
    url += 'f_date_Month=' + month + '&';
    url += 'f_date_Day=' + day + '&';
    url += 'f_hour=' + hour + '&';
    url += 'f_min=' + min + '&';
    url += 'f_wait=' + f_wait + '&';
    window.location.href = url;
  }
  return (
    <div className='container'>
      バスルート
      <div className="input-group my-3">
        <select className="form-control col" onChange={doChangeFrom} defaultValue="兎平">
          {stationdata.map((value) => (
            <option>{value.name}</option>
          ))}
        </select>
        {!isReverse ?
          <button className="input-group-text" 
                  onClick={() => setIsReverse(prevState => !prevState)}>→</button>
        :
          <button className="input-group-text" 
                  onClick={() => setIsReverse(prevState => !prevState)}>←</button>
        }
        <select className="form-control col" onChange={doChangeTo} defaultValue="多賀駅前">
          {stationdata.map((value) => (
            <option>{value.name}</option>
          ))}
        </select>
      </div>
      <DateTimePicker cName={"form-control my-3"}
                      setYear={setYear} 
                      setMonth={setMonth} 
                      setDay={setDay}
                      setHour={setHour}
                      setMinute={setMin}/>
      <button type="button" className="btn btn-primary btn mb-3" onClick={doClick}>検索</button>
      {!isReverse && from === nameToID('日立駅中央口') && <HitachiMap />}
      {!isReverse && from === nameToID('多賀駅前') && <TagaMap />}
      {isReverse && to === nameToID('日立駅中央口') && <HitachiMap />}
      {isReverse && to === nameToID('多賀駅前') && <TagaMap />}
    </div>
  )
}

export default Bus