import React, { useState } from 'react';

const DateTimePicker = ({cName, setYear, setMonth, setDay, setHour, setMinute}) => {
  const cDate = new Date();
  const [selectedDate, setSelectedDate] = useState(new Date(cDate.getFullYear(),
                                                            cDate.getMonth(),
                                                            cDate.getDate(),
                                                            cDate.getHours()+9,
                                                            cDate.getMinutes()).toISOString().slice(0,16));

  const handleChange = (event) => {
    setSelectedDate(event.target.value);
    const selectedDateTime = new Date(event.target.value);
    setYear(selectedDateTime.getFullYear());
    setMonth(selectedDateTime.getMonth() + 1);
    setDay(selectedDateTime.getDate());
    setHour(selectedDateTime.getHours());
    setMinute(selectedDateTime.getMinutes());
  };

  return (
    <div>
      <input
        className={cName}
        type="datetime-local"
        value={selectedDate}
        onChange={handleChange}
      />
    </div>
  );
};

export default DateTimePicker;